@import url(https://fonts.googleapis.com/css?family=Montserrat:600);
@import url(https://use.fontawesome.com/releases/v5.2.0/css/all.css);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.month-header {
  display: flex;
  align-items: center;
  justify-content: center; }
  .month-header .row {
    flex: 3 1; }
    .month-header .row:nth-child(2) {
      text-align: center; }
    .month-header .row:last-child {
      text-align: right; }
    .month-header .row h1 {
      font-size: 2.5em; }
    .month-header .row .fas {
      font-size: 2.4em;
      color: rgba(0, 0, 0, 0.5); }
      .month-header .row .fas:hover {
        color: white; }

.weekdays {
  font-weight: bold;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;
  background: #111111;
  border-bottom: 2px solid #000;
  color: rgba(255, 255, 255, 0.8);
  padding: 5px 0;
  line-height: 2; }
  .weekdays strong {
    flex: 1 0 14.28%;
    max-width: calc(100% / 7);
    text-align: center;
    color: white; }

form textarea,
form .rc-time-picker-input {
  font-family: inherit;
  width: 100%;
  background: rgba(0, 0, 0, 0.2);
  border: 2px solid #333;
  border-top: 0;
  border-left: 0;
  border-radius: 0;
  padding: 3px 5px;
  margin: 0;
  outline: 0; }
  form textarea::-webkit-input-placeholder, form .rc-time-picker-input::-webkit-input-placeholder {
    color: #fff; }
  form textarea:-ms-input-placeholder, form .rc-time-picker-input:-ms-input-placeholder {
    color: #fff; }
  form textarea::-ms-input-placeholder, form .rc-time-picker-input::-ms-input-placeholder {
    color: #fff; }
  form textarea, form textarea::placeholder,
  form .rc-time-picker-input,
  form .rc-time-picker-input::placeholder {
    color: #fff; }

form textarea {
  font-family: inherit; }

form .color-picker {
  display: block;
  margin-top: 3px; }

form .btn-submit,
form .btn-cancel {
  font-family: inherit;
  font-size: 13px;
  display: inline-block;
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid #333;
  border-right: 1px solid #333;
  color: #fff; }

form .btn-cancel {
  margin-left: 5px; }

.rc-time-picker-panel {
  font-family: Arial, Helvetica, sans-serif; }

.reminder {
  color: #fff;
  position: relative;
  font-size: 12px;
  padding: 3px 5px;
  margin-bottom: 3px;
  border-radius: 3px;
  box-shadow: rgba(0, 0, 0, 0.075) 0px 1px 4px 2px, rgba(0, 0, 0, 0.07) 0px 12px 6px -1px, rgba(0, 0, 0, 0.17) 0px 9px 17px 2px; }
  .reminder:hover .tools {
    visibility: visible; }
  .reminder strong {
    display: block; }
  .reminder .tools {
    position: absolute;
    top: 0;
    right: 0;
    visibility: hidden;
    background: rgba(0, 0, 0, 0.3);
    padding: 5px 3px;
    display: flex;
    justify-content: flex-end;
    z-index: 1; }
    .reminder .tools button {
      float: right; }
      .reminder .tools button .fas {
        color: #fff;
        font-size: 14px; }

.day {
  position: relative;
  flex: 1 0 14.28%;
  max-width: calc(100% / 7);
  min-height: 130px;
  padding: 10px;
  box-shadow: inset 0 1px 1px #000; }
  .day:hover .btn-new-reminder {
    opacity: 1; }
  .day.first-index-1 {
    margin-left: 14.28%; }
  .day.first-index-2 {
    margin-left: 28.56%; }
  .day.first-index-3 {
    margin-left: 42.84%; }
  .day.first-index-4 {
    margin-left: 57.12%; }
  .day.first-index-5 {
    margin-left: 71.4%; }
  .day.first-index-6 {
    margin-left: 85.68%; }
  .day.first-index-7 {
    margin-left: 99.96%; }
  .day header {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 5px; }
  .day .btn-new-reminder {
    position: absolute;
    bottom: 5px;
    right: 0;
    opacity: 0;
    -webkit-transition: opacity 500ms;
    transition: opacity 500ms;
    z-index: 1; }
    .day .btn-new-reminder .fas {
      font-size: 38px;
      color: rgba(0, 0, 0, 0.4); }
      .day .btn-new-reminder .fas:hover {
        color: #ffffff; }

.month .days {
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  flex-wrap: wrap;
  margin-top: -3px; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font: normal 1em 'Roboto', sans-serif;
  padding: 15px;
  background: -webkit-gradient(linear, left bottom, right top, from(#5c149e), color-stop(#5c149e), color-stop(#9035e3), color-stop(#761cca), to(#a862eb)) no-repeat;
  background: linear-gradient(to right top, #5c149e, #5c149e, #9035e3, #761cca, #a862eb) no-repeat;
  background-attachment: fixed; }

button {
  background: none;
  border: 0;
  outline: 0;
  cursor: pointer; }

